@import "./styles/variables";

@font-face {
  font-family: "glacial-indifference";
  src: url(styles/fonts/GlacialIndifference-Regular.otf);
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "glacial-indifference";
  src: url(styles/fonts/GlacialIndifference-Bold.otf);
  font-style: normal;
  font-weight: 600;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: $primary-font;
  background-color: $dark-grey;
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
}

.popover {
  position: absolute;
  width: 160px;
  background: $secondary-grey;
  padding: 5px;
  border-radius: 5px;
  color: white;
  font-size: 0.9rem;

  & a {
    color: white;
    text-decoration: underline;
  }
}
