@import "../../styles/variables";

.swiper {
  &-container {
    width: 100%;
    height: 100%;
  }

  &-slide {
    text-align: center;
    font-size: 18px;
    background: $secondary-grey;

    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;

    &-iframe {
      border: none;
    }
  }
}
